export default "/*///Header contact/////////////////////////*/\r\n.contact-head{\r\n    background-image: url('/assets/backgrounds/bgApple.png');\r\n    background-position: bottom;\r\n}\r\n.contact-head p{\r\n    color: grey;\r\n    font-weight: 100;\r\n    border-color: grey;\r\n}\r\n/*/// contact body////////////////////////////*/\r\n.contact{\r\n    width: 90%;\r\n    margin: 0 auto;\r\n    display: flex;\r\n    justify-content: space-around;\r\n    background-image: url('/assets/backgrounds/bgFooter.png');\r\n    background-position: center;\r\n    background-size: cover;\r\n    background-repeat: no-repeat;\r\n}\r\n.right-nav-contact .content-right-nav{\r\n    width:100%;\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: center;\r\n    position: initial;\r\n}\r\n.rmail img{\r\n    width: 45px;\r\n}\r\n.left-contact, .right-contact{\r\n    width: 35%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n}\r\n.right-contact{\r\n    padding-left: 250px;\r\n}\r\n.left-contact{\r\n    justify-content: flex-end;\r\n}\r\n.form{\r\n    max-width: 600px;\r\n    margin: 0 auto;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n}\r\ninput, textarea{\r\n    width: 95%;\r\n    height: 60px;\r\n    padding: 0 10px;\r\n    font-size: 18px;\r\n    border: 2px solid black;\r\n    border-radius: 10px;\r\n    margin: 10px 0 15px 0;\r\n}\r\n.message{\r\n    min-height: 150px;\r\n    max-height: 150px;\r\n    min-width: 95%;\r\n    max-width: 95%;\r\n    padding-top: 10px;\r\n    font-family: 'Lato', sans-serif;\r\n}\r\n.form-button{\r\n    width: 100%;\r\n    display: flex;\r\n    justify-content: space-around;\r\n}\r\nform button{\r\n    width: 200px;\r\n    height: 70px;\r\n    border-radius: 20px;\r\n    border: none;\r\n    background-color: black;\r\n    color: white;\r\n    font-size: 18px;\r\n    cursor: pointer;\r\n    transition-duration: 0.5s; \r\n}\r\nform .btnSubmit:hover{\r\n    background-color: lightseagreen;\r\n    border-color: lightseagreen;\r\n    transition-duration: 0.5s; \r\n}\r\n.btnClear{\r\n    border: 2px solid black;\r\n    background-color: white;\r\n    color: black;\r\n    transition-duration: 0.5s; \r\n}\r\n.btnClear:hover{\r\n    background-color: lightcoral;\r\n    border-color: lightcoral;\r\n    color: white;\r\n    transition-duration: 0.5s;\r\n}\r\n.reseau{\r\n    width: 100%;\r\n    margin: 100px auto 50px auto;\r\n    display: flex;\r\n    justify-content: space-between;\r\n}\r\n/*\r\n.item-reseau img{\r\n    width: 80px;\r\n    height: 80px;\r\n    transition-duration: 0.5s;\r\n}\r\n.item-reseau img:hover{\r\n    background-color: lightcoral;\r\n    border-radius: 15px;\r\n    transition-duration: 0.5s;\r\n}*/\r\n.footLink{\r\n    color: grey;\r\n    text-decoration: none;\r\n    border: 1px solid white;\r\n    transition-duration: 1.5s;\r\n}\r\n.footLink:hover{\r\n    color: black;\r\n    text-decoration: underline;\r\n    text-align: center;\r\n    border-bottom-color: black;\r\n    transition-duration: 1.5s;\r\n}\r\n.toast{\r\n    position: fixed;\r\n    top: 20px;\r\n    right: 10px;\r\n    max-width: 45%;\r\n    color: white;\r\n    padding: 10px 20px;\r\n    border-radius: 5px;\r\n    text-align: center;\r\n}\r\n.bgSuccess{\r\n    background-color: rgb(4, 235, 81);\r\n}\r\n.bgWarning{\r\n    background-color: rgb(235, 127, 4);\r\n}\r\n.infoClass{\r\n     background-color: cadetblue;\r\n}\r\n.copyright p{text-align: center;text-decoration: underline;}\r\n/*Beafor: max-width: 500px*/\r\n@media (max-width: 1200px){\r\n    /*Section footer*/\r\n    .contact{background: none;flex-direction: column-reverse;justify-content: center;}\r\n    .left-contact, .right-contact{width: 95%;margin: 0 auto;padding:0;}\r\n    .form-button{flex-direction: column;}\r\n    .form-button button{margin: 0 auto 15px auto;}\r\n    .reseau{width: 100%; margin: 20px auto;}\r\n    /*.item-reseau img{width: 65px; height: 65px;}*/\r\n    .left-contact{width: 100%;height: -webkit-max-content;height: -moz-max-content;height: max-content;padding: 15px 10px; margin: 0 auto;}\r\n    .copyright{width: 100%; height: -webkit-max-content; height: -moz-max-content; height: max-content; text-align: center;}\r\n    .reseau{\r\n        max-width: 600px;\r\n    }\r\n}\r\n@media (max-width: 800px){\r\n    .toast{\r\n        max-width: 50%;\r\n    }\r\n    .reseau{\r\n        flex-direction: column;\r\n        align-items: center;\r\n        justify-content: center;\r\n        width: 70%;\r\n    }\r\n    .footLink{\r\n        margin-top: 10px; \r\n    }\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"