export default "/* /// Navigation barre*/\r\nnav{\r\n    margin: 0;\r\n    height: 50px; \r\n    width: 100%;\r\n    background-color: black;\r\n    color: white;\r\n    position: fixed;\r\n    top: 0;\r\n    display: flex;\r\n}\r\n.navbar-nav{\r\n    margin: 0 80px;\r\n    width: -webkit-max-content;\r\n    width: -moz-max-content;\r\n    width: max-content;\r\n    height: 100%;\r\n}\r\n.nav-item{\r\n    list-style: none;\r\n    height: 100%;\r\n    width: -webkit-max-content;\r\n    width: -moz-max-content;\r\n    width: max-content;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n}\r\n.nav-link{\r\n    height: 100%;\r\n    padding: 0 25px;\r\n    display: flex;\r\n    align-items: center;\r\n    text-decoration: none;\r\n    color: white;\r\n    transition-duration: 0.5s;\r\n}\r\n.nav-link:hover{\r\n    background-color: hotpink;\r\n    transition-duration: 0.5s;\r\n}\r\n/*COntainer Message */\r\n.container{\r\n    width: 80%;\r\n    margin: 70px auto;\r\n    display: flex;\r\n    flex-direction: column-reverse;\r\n    justify-content: center;\r\n}\r\n.card{\r\n    width: 50%;\r\n    border: 5px solid gray; \r\n    border-radius: 10px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    margin: 0 auto 20px auto;\r\n}\r\n.card img{\r\n    width: 60px;\r\n    height: 60px;\r\n    border-radius: 30px;\r\n}\r\n.card-header{\r\n    text-align: center;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    background-color: grey;\r\n    padding-bottom: 10px;\r\n    color: white;\r\n}\r\n.card-body{\r\n    width: 90%;\r\n    margin: 0 auto;\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"