export default "/*/////////////////////////////////////////////////////\r\nRealisation ////////////////////////////////////\r\n////////////////////////////////////////////*/\r\n\r\n/*///////////////Head////////////////*/\r\n\r\n.real-head{\r\n    background-image: url('/assets/backgrounds/rose-gold.png');\r\n    background-position: bottom;\r\n    display: inline-block;\r\n}\r\n\r\n.real-head p{\r\n    margin-top:25px;\r\n    font-weight: 100;\r\n    color: rgb(65, 65, 65);\r\n    border-color: rgb(65, 65, 65);\r\n}\r\n\r\n/*///////////////real body///////////////*/\r\n\r\n.real{\r\n    width: 90%;\r\n    display: flex;\r\n    flex-direction: column;\r\n    justify-content: center;\r\n    margin: -125px auto 0 auto;\r\n    text-align: center;\r\n}\r\n\r\n.real-illustre img{\r\n    width: 400px;\r\n    height: 250px;\r\n    margin: 0 auto 0 auto;\r\n}\r\n\r\n.real-grid{\r\n    width: 75%;\r\n    height: 625px;\r\n    margin: 0 auto;\r\n    background-color: grey;\r\n    display: flex;\r\n    justify-content: space-around;\r\n    align-items: center;\r\n    flex-wrap: wrap;\r\n    color: rgb(212, 212, 212);\r\n}\r\n\r\n.grid-item{\r\n    width: 350px;\r\n    height: 250px;\r\n    border-radius: 5px;\r\n    background-color: black;\r\n    background-size: cover;\r\n    background-position: center;\r\n    background-repeat: no-repeat;\r\n}\r\n\r\n.desc{\r\n    width: 65%;\r\n    height: 100%;\r\n    border-radius: 5px;\r\n    background-color: rgba(0, 0, 0, 0.705);\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    justify-content: center;\r\n    margin-left: auto;\r\n}\r\n\r\n.desc p{\r\n    width: 95%;\r\n    margin: 0 auto;\r\n}\r\n\r\n.grid-item-backnode{\r\n    background-image: url('/assets/gridBackground/back-node.PNG');\r\n}\r\n\r\n.grid-item-portfolio{\r\n    background-image: url('/assets/gridBackground/portfolio.png');\r\n}\r\n\r\n.grid-item-cartech{\r\n    background-image: url('/assets/gridBackground/cartech.PNG');\r\n}\r\n\r\n.grid-item-agency{\r\n    background-image: url('/assets/gridBackground/first.png');\r\n}\r\n\r\n.list-group{\r\n    align-items: center; \r\n    text-align: left;  \r\n}\r\n\r\n.list-group .first{\r\n    border: none;\r\n}\r\n\r\n.list-group-item{\r\n    border: 1px solid white;\r\n    border-style: solid none none none;\r\n    padding: 5px 0;\r\n}\r\n\r\n.btn-box{\r\n    margin-top: 10px;\r\n    width: 100%;\r\n    display: flex;\r\n    justify-content: space-around;\r\n    align-items: center;\r\n}\r\n\r\n.btn-link{\r\n    width: 80px;\r\n    height: 30px;\r\n    text-decoration: none;\r\n    color: black;\r\n    background-color: white;\r\n    border-radius: 5px;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    transition-duration: 0.4s;\r\n}\r\n\r\n.btn-link:hover{\r\n    background-color: black;\r\n    color: white;\r\n    transition-duration: 0.4s;\r\n}\r\n\r\n.sub-title{\r\n    width: 80%;\r\n    text-align: left;\r\n    font-weight: 600;\r\n    text-decoration: underline;\r\n}\r\n\r\n/*Beafor: max-width: 500px*/\r\n\r\n@media (max-width: 1200px){\r\n    /*Section real*/\r\n    .real-grid{width: 95%; height: auto;background: none;}\r\n    .real-grid .grid-item{width: 100%; margin: 20px 0;}\r\n}\r\n\r\n@media (max-width: 500px){\r\n    .real-illustre img{\r\n        width: 100%;\r\n    }\r\n}\r\n/*# sourceMappingURL=data:application/json;base64, */"